
  html{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}
*, *::before, *::after{
    box-sizing: inherit;
    margin: 0;
}
@media (max-width: 767px){
    .upperMenu__wrapper{
        margin:0;
        padding: 0;
        box-shadow: none;
        shape-outside: none;
        display: flex;
        flex-wrap: nowrap;
        justify-content: space-around;
        background-color: #fff;
        padding-top: 15px;
        padding-bottom: 15px;
        border-bottom: 1px solid #B8FFEC;
        align-items: center;
    }
    .upperMenu__burger{
        vertical-align:middle;
        margin-left: 10px;
        margin-right: 10px;

    }
    .upperMenu__group{
        vertical-align: middle;
       
    }
    .upperMenu__divider{
        vertical-align: middle;
        margin-left: 15px;
        margin-right: 15px;
    }
    .upperMenu__repair{
        vertical-align: middle;
        /*border: 1px solid #000;
        margin-left: 10px;*/
    }
    .upperMenu__check{
        vertical-align: middle;
    }

    div.upperMenu img{
        /*-top: 15px;
        margin-bottom: 15px;
        */
        margin: 15px auto;

    }
    .about__wrapper{
        display: flex;
        align-items: center;
       
    }
    .about__highlight{
        left: 0px;
        position: absolute;
    }

    .about__title{
        margin-left: 15px;
        position: relative;

        margin-top: 20px;
        margin-bottom: 20px;
        
    }
    .services__wrapper{
        font-family: sans-serif;
        display: flex;
        flex-wrap: nowrap;
       
    }
    .services__wrapper button{
        padding: 5px;
        white-space: nowrap;
        margin-top: 10px;
        margin-bottom: 10px;
        color: 	#808080;
        background-color: #fff;
        border-radius: 5px;
        font-size: 17px;
        border: none;
        padding-left: 15px;
        padding-right: 15px;
    }
    .services__wrapper button:active, .services__wrapper button:hover{
        border: 2px solid #B8FFEC;
    }
    .services__repairees{
        margin-left: 25px;
    }

    .services__letterp{
        opacity: 0.5;
    }
    .services__lettero{
        opacity: 0.2;
    }


    .description__text{
        color:#000;
        font-family: 'Montserrat', sans-serif;
        margin: 20px 20px;
    }
    .description__button{
        background-color: #f8f8f8;
        border: #f8f8f8;
        font-size: 15px;
        font-weight: 500;
        margin-left: 20px;
        display: flex;
        align-items: center;
        padding: 5px;
        margin-bottom: 15px;
    }
    .description__button::before{
        content: url("img/expand.svg");
    
     
    }
    .description__imageMD{
        display: none;
        visibility: hidden;
    }

    html:not(div.upperMenu){
        background-color: #f8f8f8;
    }

    .upperMenu__call, .upperMenu__chat, .upperMenu__profile, .upperMenu__dividerFirst{
        display: none;
        visibility: hidden;
    }
    .description__p2{
        display: none;
        visibility: hidden;
    }
    .btn{
        display: none;
        visibility: hidden;
    }
    .labels{
        display: none;
        visibility: hidden;
    }
    .swiper-wrapper{
        margin-left: 20px;
        margin-bottom: 40px;
    }
}

